@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  overflow-y: auto;
}

body {
  font-family: 'Robot', sans-serif;
}

.logo {
  margin-top: 12%;
}


.animate {
  opacity: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(/react-weather-app/static/media/animate.ae6a8b1c.png);
  background-size: cover;
  -webkit-animation: animate 130s linear infinite;
          animation: animate 130s linear infinite;
}

@-webkit-keyframes animate {
  0%
  {
    background-position: 0px;
  }
  100%
  {
    background-position: 5440px;
  }
}

@keyframes animate {
  0%
  {
    background-position: 0px;
  }
  100%
  {
    background-position: 5440px;
  }
}

.search {
  background: transparent;
  outline: none;
  color: #c5ecfd;
  width: 250px;
  border: 0;
  border-bottom: 2px solid white;
  margin-bottom: 25px;
  font-size: 18px;
}

::-webkit-input-placeholder {
  color: #c5ecfd;
}

.weather {
  font-size: 20px;
}
